.list {
    display: flex;
    flex-wrap: wrap;
    &__wrap {
        display: flex;
        width: 100%;
        gap: 10px;
        margin-bottom: 10px;
    }
    a {
        flex: 1;
        position: relative;
        img {
            display: block;
            width: 100%;
            height: auto;
        }

        .download-svg {
            background-color: rgba(0, 0, 0, 0.5);
            max-width: 30px;
            position: absolute;
            bottom: 4px;
            left: 12px;
        }
    }
    .piece-wrap {
        display: flex;
        flex-direction: column;
        gap: 10px;
        flex: 1;
    }
    .piece-wrap-flex {
        flex-direction: row !important;
    }
    .btn-down-all {
        width: 100%;
    }
}
@media (max-width: 767px) {
    .list {
        width: 100%;
    }
}
