.c_container{
  padding:50px;
  box-sizing: border-box;
  width: 100%;
  max-width: 1200px;
  margin: auto;
}
.uploade-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  max-width:1920px;
  margin-bottom: 30px;
}
.button-body{
  padding:0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media(max-width:767px){
  .c_container{
    flex-wrap:wrap;
  }
  .button-body{
    width:100%;
    margin: 30px 0;
  }
}
.btn{
  display:flex;
  height: 50px;
  align-items: center;
  padding:0 20px;
  border:1px solid #000;
  border-radius: 20px;
  background-color: #5E496D;
  color:#FFF;
  margin:0 auto;
  justify-content: center;
}
.quantity{
  display:flex;
  align-items: center;
  gap:10px;
}
.quantity input[type="button"]{
  display:flex;
  width:35px;
  height: 35px;
  align-items: center;
  justify-content: center;
  line-height: 0;
  background-color: #5E496D;
  color: #fff;
  cursor: pointer;
  border-radius: 50%;
  padding: 0;
  font-size: 22px;
  border: none!important;
}
.quantity input[type="text"]{
  border:none;
  background: transparent;
  text-align: center;
  max-width:50px;
  font-size: 22px;
}
.quantity input[type="text"]:focus{
  outline:0;
  border:none;

}