.switch{
    display: flex;
    width:max-content;
    margin:20px 0;
    border:1px solid #000;
    border-radius:10px;
    overflow:hidden;
    input{
        display:none;
    }
    label{
        display:flex;
        height: 50px;
        padding:0 clamp(10px,1.042vw,20px);
        align-items: center;
        justify-content: center; 
        width:max-content;
        cursor: pointer;
    }
    label:hover {
        background-color: #967fa6;
        color:#FFF;
    }
    input:checked + label { 
        background-color: #5E496D;
        color:#FFF;
    }

}