.cut-btn {
    display: flex;
    width: 100%;
    height: 50px;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
    border: 1px solid #000;
    border-radius: 20px;
    background-color: #5e496d;
    color: #fff;
    min-width: 100px;
}

.cut-btn-wrapper {
    width: 100%;
}
