.uploade {
    max-width: 500px;
    position: relative;
    padding: 20px;
    border: 1px dashed #000;

    #upload-file {
        margin-top: 12px;
    }
    #upload-file::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }

    img {
        width: 100%;
        height: 100%;
        /*min-width:500px;
        min-height: 500px;*/
        max-width: 500px;
        max-height: 500px;
        object-fit: contain;
    }
    &-img-wrap {
        position: relative;
        .gallery-div {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-size: 34% 16.67%;
            background-image: linear-gradient(
                    to right,
                    #5e496d 4px,
                    transparent 1px
                ),
                linear-gradient(to bottom, #5e496d 4px, transparent 1px);
            background-position: -2px -2px, -5px -5px;
            box-shadow: inset 0px 0px 0px 6px white;
            background-color: rgba(215, 219, 236, 0.3);
        }
    }
}
.choose-btn {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    pointer-events: none;
    border-radius: 0;
}
@media (max-width: 767px) {
    .uploade {
        width: 100%;
    }
}
